.auto-scroll-bar-container {
  height: auto;
  width: 100vw;
  background-color: #1551f6;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  position: relative;
  overflow: hidden; /* Ensure only the auto-scroll bar content overflows */
}

.auto-scroll-bar-content {
  display: flex;
  align-items: center;
  font-size: 2rem;
  font-weight: bold;
  white-space: nowrap;
  height: auto;
  color: white;
}

.auto-scroll-bar-text {
  margin: 0 50px;
}

.auto-scroll-bar-gif {
  width: 9rem; /* Adjust the width as needed */
  height: auto; /* Maintain aspect ratio */
  margin: 0 50px;
}

/* Media Queries for Responsiveness */
@media (max-width: 767px) {
  .auto-scroll-bar-content {
    font-size: 1.2rem; /* Smaller font size for mobile */
  }

  .auto-scroll-bar-gif {
    width: 6rem; /* Smaller GIF size for mobile */
  }

  .auto-scroll-bar-text {
    margin: 0 10px; /* Reduce margin for mobile */
  }
}
